html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

.profile {
  width: 425px;
  text-align: center;
  margin: auto;
  border: solid 1px #d8e0ea;
}

.description {
  width: 425px;
  height: auto;
}

.avatar {
  height: auto;
  border-radius: 50%;
  overflow: hidden;
  text-align: center;
  margin: auto;
}

.name {
  font-size: 20px;
  color: #333333;
  font-weight: 700;
  margin-top: 30px;
}

.tag {
  font-size: 16px;
  color: #666666;
  text-align: center;
  margin-top: 10px;
}

.location {
  font-size: 16px;
  color: #97a2ae;
  line-height: 32px;
  margin-top: 10px;
  margin-bottom: 30px;
}

.stats {
  display: flex;
  justify-content: space-evenly;
  padding-top: 15px;
  padding-bottom: 15px;
  list-style: none;
  background-color: #f3f6f9;
  border-radius: 0 0 5px 5px;
}

.list {
  flex-direction: column;
  align-items: center;
  border: solid 1px #d8e0ea;
  width: 85px;
}

.label {
  margin-top: 0;
  color: #97a2ae;
  font-size: 14px;
  display: flex;
}

.quantity {
  line-height: 1.2;
  font-weight: bold;
  font-size: 14px;
  display: flex;
}
