.friend_list {
  width: 425px;
  height: auto;
  margin: auto;
  border: solid 1px rgb(211, 221, 221);
}

.item_friend {
  display: flex;
  align-items: center;
  margin: 20px;
  box-shadow: 0 0 1em;
  padding-left: 10px;
  border-radius: 3px;
}

.statusOnline,
.statusOffline {
  display: block;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: rgb(111, 233, 111);
  margin: 0 15px 0 15px;
}

.statusOffline {
  background-color: rgb(233, 99, 99);
}

.avatar {
  border-radius: 10px;
  margin: 10px;
}

.name {
  margin-top: auto;
  margin-bottom: auto;
  font-size: 24px;
  font-weight: 600;
}
